// 
@mixin font-face($name, $filename, $weight, $joinName: $name, $style: normal) {
  @font-face {
    font-family: $name;
    font-weight: $weight;
    font-style: $style;
    font-display: swap;
    src: url("../fonts/" + $filename + ".eot"); /* IE9 Compat Modes */
    src: local(""),
      url("../fonts/" + $filename + ".eot?#iefix") format("embedded-opentype"),
      /* IE6-IE8 */ url("../fonts/" + $filename + ".woff2") format("woff2"),
      /* Modern Browsers */ url("../fonts/" + $filename + ".woff")
        format("woff"),
      /* Modern Browsers */ url("../fonts/" + $filename + ".ttf")
        format("truetype"),
      /* Safari, Android, iOS */
        url("../fonts/" + $filename + ".svg#" + $joinName) format("svg"); /* Legacy iOS */
  }
}

//
@mixin mobile {
  @media (max-width: ($mobile - 1px)) {
    @content;
  }
}
